<template>
  <div class="flex flex-col gap-6">
    <TableView v-model:current-page="currentPage" v-model:page-size="pageSize" :all-pages-count="allPagesCount" :page-size-options="pageSizeOptions" @table-options-changed="getData">
      <template #filter-additional-content>
        <div class="flex justify-between align-center w-full">
          <div class="grid grid-cols-5 gap-3 w-[80%]">
            <div class="col-span-2">
              <DateComponent
                v-model="date"
                :options="{
                  range: true,
                  multiCalendars: true,
                  maxDate: dayjs().endOf('year').format('YYYY-MM-DD'),
                }"
                placeholder="Boshlanish va tugash vaqti"
              />
            </div>
            <input v-validate-cyrillic id="name" v-model="name" name="name" class="base_input" placeholder="F.I.Sh" />
            <input v-validate-cyrillic id="amount" v-model="amount" name="name" class="base_input" placeholder="Summa" />
            <div v-if="isSuperuserRole" class="bg-white">
              <SelectBox id="center" v-model="markaz" class="w-full" :options="markazsList" show-by="name" track-by="id" placeholder="Markazni tanlang" />
            </div>
          </div>
          <div class="flex items-center">
            <router-link to="/add-donations" class="text-white bg-[#4785FE] px-[12px] py-[10px] flex gap-[17px] item-center rounded-[5px] tracking-wide">
              <Icon icon="mdi:plus" class="text-white w-[24px] h-[24px]" />
              Xayriya qo‘shish
            </router-link>
          </div>
        </div>
      </template>
      <template #table-content>
        <table>
          <thead>
            <tr>
              <th class="w-28">T/r</th>
              <th v-for="header in headers" :key="header.value" :class="[header.class, header.value]">
                {{ header.text }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in donations" :key="index">
              <td class="w-28">{{ index + 1 }}</td>
              <td v-for="header in headers" :key="header.value" :title="headerValue(item, header)" :class="[header.class, header.value]">
                <span v-if="header.value !== 'edit'">{{ headerValue(item, header) }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </template>
    </TableView>
  </div>
</template>

<script setup>
import {ref, watch, computed} from 'vue';
import DateComponent from '../components/Common/DateComponent';
import dayjs from 'dayjs';
import {Icon} from '@iconify/vue';
import TableView from '../components/Tables/TableView.vue';
import useDonation from '../composables/useDonations';
import useTableOptions from '../composables/useTableOptions';
import useMarkazs from '../composables/selectbox-list/useMarkazs';
import SelectBox from '../components/Common/SelectBox.vue';
import {useStore} from 'vuex';

const SUPERUSER_ROLE = 'Apparat';

const store = useStore();
const isSuperuserRole = computed(() => store.getters['getUserInfo'].role === SUPERUSER_ROLE);

const {getDonationsList, donations} = useDonation();
const {markazsList} = useMarkazs();
const {currentPage, pageSize, allPagesCount, pageSizeOptions} = useTableOptions([5, 10, 50]);

const headers = ref([
  {
    text: 'F.I.Sh/Tashkilot nomi',
    value: 'name',
  },
  {
    text: 'Sana',
    value: 'date',
  },
  {
    text: 'Summa',
    value: 'amount',
  },
]);

const date = ref('');
const name = ref('');
const amount = ref('');
const markaz = ref('');

const headerValue = (item, header) => {
  if (Array.isArray(header.value)) {
    return item[header.value[0]][header.value[1]];
  } else {
    return item[header.value];
  }
};

const getData = async (params) => {
  const data = await getDonationsList(params);
  donations.value = data?.results;
  allPagesCount.value = data?.num_pages;
};

watch(
  [currentPage, pageSize, date, name, amount, markaz],
  async ([page, page_size, d, n, a, markaz]) => {
    let [start_date, end_date] = d ? d.map((n) => new Date(n).toISOString().split('T')[0]) : ['', ''];
    await getData({
      name: n,
      amount: a,
      start_date,
      end_date,
      markaz,
      page,
      page_size,
    });
  },
  {immediate: true}
);
</script>

