  text-align: start;
  font-weight: 500;
  font-size: 1rem;
  line-height: 130%;
}

td {
  padding: 0 0.625rem;
  height: 2.375rem;
}

tbody td:first-child {
  padding-left: 1.875rem;
}

tbody td:last-child {
  padding-right: 1.875rem;
}

thead th:first-child {
  padding-left: 1.875rem;
}

