import {onMounted, ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useMarkazs() {
  const markazsList = ref([]);
  const fetchPositions = async () => {
    const {data} = await InfoService.fetchMarkazs();
    markazsList.value = [
      {
        id: '',
        region: 'Toshkent shahri',
        name: 'Markazni tanlang',
      },
      ...data,
    ];
  };
  onMounted(async () => {
    await fetchPositions();
  });

  return {
    markazsList,
  };
}
