import axios from './api/axios';

export default {
  async getDonationsNotificationList() {
    return await axios.get('api/notifications/donation/notification/');
  },
  async getDonationsList(params) {
    return await axios.get('api/donations/', {params});
  },
  async getDonationsNotificationComplete(id) {
    return await axios.get(`api/notifications/donation/complete/${id}/`);
  },
  async getTotalDonations() {
    return await axios.get('api/donations/total_amount/');
  },
  async createDonation(data) {
    return await axios.post('api/donations/', data);
  },
};
