import {ref} from 'vue';
import DonationService from '../services/donation.service';

export default function useDonation() {
  const donations = ref([]);
  async function getDonationsList(params) {
    try {
      const resp = await DonationService.getDonationsList(params);
      return resp.data;
    } catch (error) {
      console.log({error});
    }
  }

  async function getDonationComplete(id) {
    try {
      const resp = await DonationService.getDonationsNotificationComplete(id);
      return resp.data;
    } catch (error) {
      console.log({error});
    }
  }

  async function createDonation(data) {
    try {
      const resp = await DonationService.createDonation(data);
      return resp.data;
    } catch (error) {
      console.log({error});
    }
  }

  return {
    donations,
    getDonationsList,
    getDonationComplete,
    createDonation,
  };
}
